import {useState, useEffect} from 'react'
import Web3 from 'web3'
import abis from './utils/abis'

import Pool from './components/Pool'
import Symbol from './components/Symbol'
import Liquidation from './components/Liquidation'

const ZERO_ADDRESS = '0x0000000000000000000000000000000000000000'

function nn(value, unit='ether') {
    return Web3.utils.fromWei(value.toString(), unit)
}

function bb(value, unit='ether') {
    return Web3.utils.toWei(value.toString(), unit)
}

function App() {

    const web3 = new Web3(window.ethereum)

    const busd = new web3.eth.Contract(abis.ERC20, '0x2ebE70929bC7D930248040f54135dA12f458690C')
    const cake = new web3.eth.Contract(abis.ERC20, '0x55772f57711288F76c9989D835E3Ba98082FBdef')

    const pool = new web3.eth.Contract(abis.PoolImplementation, '0x3Dadb845D10390A347C44CF09a130c99Faef27a2')

    const symbolBTCUSD = new web3.eth.Contract(abis.SymbolImplementationFutures, '0x5B03C8db44cd0d0B54DD98825329962bbE936682')
    const symbolETHUSD = new web3.eth.Contract(abis.SymbolImplementationFutures, '0xa21f3E83Dc05c052F2A1e2764262c8D60B60d72e')
    const symbolOptionBTCUSD = new web3.eth.Contract(abis.SymbolImplementationOption, '0x4b24ea73678fa3c94471c7C416AAcD9fd9553878')
    const symbolOptionETHUSD = new web3.eth.Contract(abis.SymbolImplementationOption, '0x3782457b76C89761d74b40697e4f413F4707eAEe')

    const lToken = new web3.eth.Contract(abis.DToken, '0xeF2A6BF75b1dE11dE09786e1157769E8C63C561c')
    const pToken = new web3.eth.Contract(abis.DToken, '0x9d9058Fbfb9cE644d6b4f80382e4f853e901197a')

    const [data, setData] = useState({
        ipool: {
            address: '0x0000000000000000000000000000000000000000',
            balanceBNB: 0,
            balanceBUSD: 0,
            balanceCAKE: 0,
            liquidity: 0,
            lpsPnl: 0,
            cumulativePnlPerLiquidity: 0,
            protocolFeeAccrued: 0,
        },
        iaccount: {
            address: '0x0000000000000000000000000000000000000000',
            balanceBNB: 0,
            balanceBUSD: 0,
            balanceCAKE: 0,
            liquidity: 0,
            margin: 0,
        },
        iSymbolBTCUSD: {
            symbol: '',
            netVolume: 0,
            netCost: 0,
            indexPrice: 0,
            fundingTimestamp: 0,
            cumulativeFundingPerVolume: 0,
            tradersPnl: 0,
            initialMarginRequired: 0,
            nPositionHolders: 0,
            iVolume: 0,
            iCost: 0,
            iCumulativeFundingPerVolume: 0,
        },
        iSymbolETHUSD: {
            symbol: '',
            netVolume: 0,
            netCost: 0,
            indexPrice: 0,
            fundingTimestamp: 0,
            cumulativeFundingPerVolume: 0,
            tradersPnl: 0,
            initialMarginRequired: 0,
            nPositionHolders: 0,
            iVolume: 0,
            iCost: 0,
            iCumulativeFundingPerVolume: 0,
        },
        iSymbolOptionBTCUSD: {
            symbol: '',
            netVolume: 0,
            netCost: 0,
            indexPrice: 0,
            fundingTimestamp: 0,
            cumulativeFundingPerVolume: 0,
            tradersPnl: 0,
            initialMarginRequired: 0,
            nPositionHolders: 0,
            iVolume: 0,
            iCost: 0,
            iCumulativeFundingPerVolume: 0,
        },
        iSymbolOptionETHUSD: {
            symbol: '',
            netVolume: 0,
            netCost: 0,
            indexPrice: 0,
            fundingTimestamp: 0,
            cumulativeFundingPerVolume: 0,
            tradersPnl: 0,
            initialMarginRequired: 0,
            nPositionHolders: 0,
            iVolume: 0,
            iCost: 0,
            iCumulativeFundingPerVolume: 0,
        },
    })

    useEffect(() => {
        update()
    }, [])

    const update = async () => {
        const address = Web3.utils.toChecksumAddress((await window.ethereum.request({method: 'eth_requestAccounts'}))[0])
        const ipool = await getPool()
        const iaccount = await getAccount(address)
        const iSymbolBTCUSD = await getSymbol(1, address)
        const iSymbolETHUSD = await getSymbol(2, address)
        const iSymbolOptionBTCUSD = await getSymbol(3, address)
        const iSymbolOptionETHUSD = await getSymbol(4, address)
        setData({
            iaccount, ipool, iSymbolBTCUSD, iSymbolETHUSD, iSymbolOptionBTCUSD, iSymbolOptionETHUSD
        })
    }

    const getPool = async () => {
        return {
            address: pool.options.address,
            balanceBNB: nn(await web3.eth.getBalance(pool.options.address)),
            balanceBUSD: nn(await busd.methods.balanceOf(pool.options.address).call()),
            balanceCAKE: nn(await cake.methods.balanceOf(pool.options.address).call()),
            liquidity: nn(await pool.methods.liquidity().call()),
            lpsPnl: nn(await pool.methods.lpsPnl().call()),
            cumulativePnlPerLiquidity: nn(await pool.methods.cumulativePnlPerLiquidity().call()),
            protocolFeeAccrued: nn(await pool.methods.protocolFeeAccrued().call())
        }
    }

    const getAccount = async (address) => {
        return {
            address: address,
            balanceBNB: nn(await web3.eth.getBalance(address)),
            balanceBUSD: nn(await busd.methods.balanceOf(address).call()),
            balanceCAKE: nn(await cake.methods.balanceOf(address).call()),
            liquidity: await getLiquidity(address),
            margin: await getMargin(address),
        }
    }

    const getSymbol = async (id, address) => {
        const symbol = id === 1 ? symbolBTCUSD : (id === 2 ? symbolETHUSD : (id === 3 ? symbolOptionBTCUSD : symbolOptionETHUSD))
        const pTokenId = await pToken.methods.getTokenIdOf(address).call()
        const d = {
            symbol: await symbol.methods.symbol().call(),
            netVolume: nn(await symbol.methods.netVolume().call()),
            netCost: nn(await symbol.methods.netCost().call()),
            indexPrice: nn(await symbol.methods.indexPrice().call()),
            fundingTimestamp: await symbol.methods.fundingTimestamp().call(),
            cumulativeFundingPerVolume: nn(await symbol.methods.cumulativeFundingPerVolume().call()),
            tradersPnl: nn(await symbol.methods.tradersPnl().call()),
            initialMarginRequired: nn(await symbol.methods.initialMarginRequired().call()),
            nPositionHolders: await symbol.methods.nPositionHolders().call(),
        }
        if (pTokenId != 0) {
            const p = await symbol.methods.positions(pTokenId).call()
            d.iVolume = nn(p.volume)
            d.iCost = nn(p.cost)
            d.iCumulativeFundingPerVolume = nn(p.cumulativeFundingPerVolume)
        }
        return d
    }

    const getLiquidity = async (address) => {
        const lTokenId = await lToken.methods.getTokenIdOf(address).call()
        if (lTokenId != 0) {
            const info = await pool.methods.lpInfos(lTokenId).call()
            return nn(info.liquidity)
        } else {
            return 0
        }
    }

    const getMargin = async (address) => {
        const pTokenId = await pToken.methods.getTokenIdOf(address).call()
        if (pTokenId != 0) {
            const info = await pool.methods.tdInfos(pTokenId).call()
            const vault = new web3.eth.Contract(abis.VaultImplementation, info.vault)
            return nn(new web3.utils.BN(info.amountB0).add(new web3.utils.BN(await vault.methods.getBorrowLimit().call())))
        } else {
            return 0
        }
    }

    const onMint = async (id) => {
        if (id === 1) {
            await busd.methods.mint(data.iaccount.address, bb(10000)).send({from: data.iaccount.address})
        } else if (id === 2) {
            await cake.methods.mint(data.iaccount.address, bb(500)).send({from: data.iaccount.address})
        }
        await update()
    }

    const onApprove = async (id) => {
        const max = '0xffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff'
        if (id === 1) {
            await busd.methods.approve(pool.options.address, max).send({from: data.iaccount.address})
        } else if (id === 2) {
            await cake.methods.approve(pool.options.address, max).send({from: data.iaccount.address})
        }
    }

    const onAddLiquidity = async (id, value) => {
        if (id === 0) {
            await pool.methods.addLiquidity(ZERO_ADDRESS, '0').send({from: data.iaccount.address, value: bb(value)})
        } else if (id === 1) {
            await pool.methods.addLiquidity(busd.options.address, bb(value)).send({from: data.iaccount.address})
        } else if (id === 2) {
            await pool.methods.addLiquidity(cake.options.address, bb(value)).send({from: data.iaccount.address})
        }
        await update()
    }

    const onRemoveLiquidity = async (id, value) => {
        const addr = id === 0 ? ZERO_ADDRESS : (id === 1 ? busd.options.address : cake.options.address)
        await pool.methods.removeLiquidity(addr, bb(value)).send({from: data.iaccount.address})
        await update()
    }

    const onAddMargin = async (id, value) => {
        if (id === 0) {
            await pool.methods.addMargin(ZERO_ADDRESS, '0').send({from: data.iaccount.address, value: bb(value)})
        } else if (id === 1) {
            await pool.methods.addMargin(busd.options.address, bb(value)).send({from: data.iaccount.address})
        } else if (id === 2) {
            await pool.methods.addMargin(cake.options.address, bb(value)).send({from: data.iaccount.address})
        }
        await update()
    }

    const onRemoveMargin = async (id, value) => {
        const addr = id === 0 ? ZERO_ADDRESS : (id === 1 ? busd.options.address : cake.options.address)
        await pool.methods.removeMargin(addr, bb(value)).send({from: data.iaccount.address})
        await update()
    }

    const onTrade = async (id, volume) => {
        const sym = id === 1 ? 'BTCUSD' : (id === 2 ? 'ETHUSD' : (id === 3 ? 'BTCUSD-60000-C' : 'ETHUSD-5000-P'))
        await pool.methods.trade(sym, bb(volume)).send({from: data.iaccount.address})
        await update()
    }

    const onLiquidate = async (address) => {
        const pTokenId = await pToken.methods.getTokenIdOf(address).call()
        if (pTokenId != 0) {
            await pool.methods.liquidate(pTokenId).send({from: data.iaccount.address})
        }
        await update()
    }

    return (
        <div className="App">
            <h1>DeriV3 BSC Testnet</h1>
            <Pool
                ipool={data.ipool}
                iaccount={data.iaccount}
                onMint={onMint}
                onApprove={onApprove}
                onAddLiquidity={onAddLiquidity}
                onRemoveLiquidity={onRemoveLiquidity}
                onAddMargin={onAddMargin}
                onRemoveMargin={onRemoveMargin}
            />
            <br/>
            <Symbol
                iSymbolBTCUSD={data.iSymbolBTCUSD}
                iSymbolETHUSD={data.iSymbolETHUSD}
                iSymbolOptionBTCUSD={data.iSymbolOptionBTCUSD}
                iSymbolOptionETHUSD={data.iSymbolOptionETHUSD}
                onTrade={onTrade}
            />
            <br/>
            <Liquidation onLiquidate={onLiquidate} />
        </div>
    )

}

export default App

