import {useState} from 'react'

const Symbol = ({iSymbolBTCUSD, iSymbolETHUSD, iSymbolOptionBTCUSD, iSymbolOptionETHUSD, onTrade}) => {
    const [volumeBTCUSD, setVolumeBTCUSD] = useState(0)
    const [volumeETHUSD, setVolumeETHUSD] = useState(0)
    const [volumeOptionBTCUSD, setVolumeOptionBTCUSD] = useState(0)
    const [volumeOptionETHUSD, setVolumeOptionETHUSD] = useState(0)

    return (
        <div>
            <table border="1">
            <tbody>
                <tr>
                    <th align="left">Symbol</th>
                    <td>{iSymbolBTCUSD.symbol}</td>
                    <td>{iSymbolETHUSD.symbol}</td>
                    <td>{iSymbolOptionBTCUSD.symbol}</td>
                    <td>{iSymbolOptionETHUSD.symbol}</td>
                </tr>
                <tr>
                    <th align="left">netVolume</th>
                    <td>{iSymbolBTCUSD.netVolume}</td>
                    <td>{iSymbolETHUSD.netVolume}</td>
                    <td>{iSymbolOptionBTCUSD.netVolume}</td>
                    <td>{iSymbolOptionETHUSD.netVolume}</td>
                </tr>
                <tr>
                    <th align="left">netCost</th>
                    <td>{iSymbolBTCUSD.netCost}</td>
                    <td>{iSymbolETHUSD.netCost}</td>
                    <td>{iSymbolOptionBTCUSD.netCost}</td>
                    <td>{iSymbolOptionETHUSD.netCost}</td>
                </tr>
                <tr>
                    <th align="left">indexPrice</th>
                    <td>{iSymbolBTCUSD.indexPrice}</td>
                    <td>{iSymbolETHUSD.indexPrice}</td>
                    <td>{iSymbolOptionBTCUSD.indexPrice}</td>
                    <td>{iSymbolOptionETHUSD.indexPrice}</td>
                </tr>
                <tr>
                    <th align="left">fundingTimestamp</th>
                    <td>{iSymbolBTCUSD.fundingTimestamp}</td>
                    <td>{iSymbolETHUSD.fundingTimestamp}</td>
                    <td>{iSymbolOptionBTCUSD.fundingTimestamp}</td>
                    <td>{iSymbolOptionETHUSD.fundingTimestamp}</td>
                </tr>
                <tr>
                    <th align="left">cumulativeFundingPerVolume</th>
                    <td>{iSymbolBTCUSD.cumulativeFundingPerVolume}</td>
                    <td>{iSymbolETHUSD.cumulativeFundingPerVolume}</td>
                    <td>{iSymbolOptionBTCUSD.cumulativeFundingPerVolume}</td>
                    <td>{iSymbolOptionETHUSD.cumulativeFundingPerVolume}</td>
                </tr>
                <tr>
                    <th align="left">tradersPnl</th>
                    <td>{iSymbolBTCUSD.tradersPnl}</td>
                    <td>{iSymbolETHUSD.tradersPnl}</td>
                    <td>{iSymbolOptionBTCUSD.tradersPnl}</td>
                    <td>{iSymbolOptionETHUSD.tradersPnl}</td>
                </tr>
                <tr>
                    <th align="left">initialMarginRequired</th>
                    <td>{iSymbolBTCUSD.initialMarginRequired}</td>
                    <td>{iSymbolETHUSD.initialMarginRequired}</td>
                    <td>{iSymbolOptionBTCUSD.initialMarginRequired}</td>
                    <td>{iSymbolOptionETHUSD.initialMarginRequired}</td>
                </tr>
                <tr>
                    <th align="left">nPositionHolders</th>
                    <td>{iSymbolBTCUSD.nPositionHolders}</td>
                    <td>{iSymbolETHUSD.nPositionHolders}</td>
                    <td>{iSymbolOptionBTCUSD.nPositionHolders}</td>
                    <td>{iSymbolOptionETHUSD.nPositionHolders}</td>
                </tr>
                <tr bgcolor="yellow">
                    <th align="left">Your Volume</th>
                    <td>{iSymbolBTCUSD.iVolume}</td>
                    <td>{iSymbolETHUSD.iVolume}</td>
                    <td>{iSymbolOptionBTCUSD.iVolume}</td>
                    <td>{iSymbolOptionETHUSD.iVolume}</td>
                </tr>
                <tr bgcolor="yellow">
                    <th align="left">Your Cost</th>
                    <td>{iSymbolBTCUSD.iCost}</td>
                    <td>{iSymbolETHUSD.iCost}</td>
                    <td>{iSymbolOptionBTCUSD.iCost}</td>
                    <td>{iSymbolOptionETHUSD.iCost}</td>
                </tr>
                <tr bgcolor="yellow">
                    <th align="left">Your CumulativeFundingPerVolume</th>
                    <td>{iSymbolBTCUSD.iCumulativeFundingPerVolume}</td>
                    <td>{iSymbolETHUSD.iCumulativeFundingPerVolume}</td>
                    <td>{iSymbolOptionBTCUSD.iCumulativeFundingPerVolume}</td>
                    <td>{iSymbolOptionETHUSD.iCumulativeFundingPerVolume}</td>
                </tr>
                <tr bgcolor="lightgreen">
                    <th align="left">Trade</th>
                    <td><input type='text' value={volumeBTCUSD} onChange={(e) => setVolumeBTCUSD(e.target.value)} /></td>
                    <td><input type='text' value={volumeETHUSD} onChange={(e) => setVolumeETHUSD(e.target.value)} /></td>
                    <td><input type='text' value={volumeOptionBTCUSD} onChange={(e) => setVolumeOptionBTCUSD(e.target.value)} /></td>
                    <td><input type='text' value={volumeOptionETHUSD} onChange={(e) => setVolumeOptionETHUSD(e.target.value)} /></td>
                </tr>
                <tr>
                    <th/>
                    <td><button onClick={() => onTrade(1, volumeBTCUSD)}>Trade</button></td>
                    <td><button onClick={() => onTrade(2, volumeETHUSD)}>Trade</button></td>
                    <td><button onClick={() => onTrade(3, volumeOptionBTCUSD)}>Trade</button></td>
                    <td><button onClick={() => onTrade(4, volumeOptionETHUSD)}>Trade</button></td>
                </tr>
            </tbody>
            </table>
        </div>
    )
}

export default Symbol

