import {useState} from 'react'

const Pool = ({ipool, iaccount, onMint, onApprove, onAddLiquidity, onRemoveLiquidity, onAddMargin, onRemoveMargin}) => {
    const [amountBNB, setAmountBNB] = useState(0)
    const [amountBUSD, setAmountBUSD] = useState(0)
    const [amountCAKE, setAmountCAKE] = useState(0)
    return (
        <div>
            <table border="1">
            <tbody>
                <tr>
                    <th>Pool Info</th>
                    <th>liquidity</th>
                    <th>lpsPnl</th>
                    <th>cumulativePnlPerLiquidity</th>
                    <th>protocolFeeAccrued</th>
                </tr>
                <tr>
                    <th>Value</th>
                    <td>{ipool.liquidity}</td>
                    <td>{ipool.lpsPnl}</td>
                    <td>{ipool.cumulativePnlPerLiquidity}</td>
                    <td>{ipool.protocolFeeAccrued}</td>
                </tr>
            </tbody>
            </table>
            <br/>
            <table border="1">
            <tbody>
                <tr>
                    <th>Your Info</th>
                    <th>Liquidity</th>
                    <th>Margin</th>
                </tr>
                <tr bgcolor="yellow">
                    <th/>
                    <td>{iaccount.liquidity}</td>
                    <td>{iaccount.margin}</td>
                </tr>
            </tbody>
            </table>
            <br/>
            <table border="1">
            <tbody>
                <tr>
                    <th></th>
                    <th>BNB</th>
                    <th>BUSD</th>
                    <th>CAKE</th>
                </tr>
                <tr>
                    <td>Pool {ipool.address}</td>
                    <td>{ipool.balanceBNB}</td>
                    <td>{ipool.balanceBUSD}</td>
                    <td>{ipool.balanceCAKE}</td>
                </tr>
                <tr bgcolor="yellow">
                    <td>You {iaccount.address}</td>
                    <td>{iaccount.balanceBNB}</td>
                    <td>{iaccount.balanceBUSD}</td>
                    <td>{iaccount.balanceCAKE}</td>
                </tr>
                <tr>
                    <td/>
                    <td/>
                    <td><button onClick={() => onMint(1)}>Mint</button></td>
                    <td><button onClick={() => onMint(2)}>Mint</button></td>
                </tr>
                <tr>
                    <td/>
                    <td/>
                    <td><button onClick={() => onApprove(1)}>Approve</button></td>
                    <td><button onClick={() => onApprove(2)}>Approve</button></td>
                </tr>
                <tr bgcolor="yellow">
                    <td>Amount</td>
                    <td><input type='text' value={amountBNB} onChange={(e) => setAmountBNB(e.target.value)}  /></td>
                    <td><input type='text' value={amountBUSD} onChange={(e) => setAmountBUSD(e.target.value)}/></td>
                    <td><input type='text' value={amountCAKE} onChange={(e) => setAmountCAKE(e.target.value)}/></td>
                </tr>
                <tr>
                    <td/>
                    <td><button onClick={() => onAddLiquidity(0, amountBNB)}>AddLiquidity</button></td>
                    <td><button onClick={() => onAddLiquidity(1, amountBUSD)}>AddLiquidity</button></td>
                    <td><button onClick={() => onAddLiquidity(2, amountCAKE)}>AddLiquidity</button></td>
                </tr>
                <tr>
                    <td/>
                    <td><button onClick={() => onRemoveLiquidity(0, amountBNB)}>RemoveLiquidity</button></td>
                    <td><button onClick={() => onRemoveLiquidity(1, amountBUSD)}>RemoveLiquidity</button></td>
                    <td><button onClick={() => onRemoveLiquidity(2, amountCAKE)}>RemoveLiquidity</button></td>
                </tr>
                <tr>
                    <td/>
                    <td><button onClick={() => onAddMargin(0, amountBNB)}>AddMargin</button></td>
                    <td><button onClick={() => onAddMargin(1, amountBUSD)}>AddMargin</button></td>
                    <td><button onClick={() => onAddMargin(2, amountCAKE)}>AddMargin</button></td>
                </tr>
                <tr>
                    <td/>
                    <td><button onClick={() => onRemoveMargin(0, amountBNB)}>RemoveMargin</button></td>
                    <td><button onClick={() => onRemoveMargin(1, amountBUSD)}>RemoveMargin</button></td>
                    <td><button onClick={() => onRemoveMargin(2, amountCAKE)}>RemoveMargin</button></td>
                </tr>
            </tbody>
            </table>
        </div>
    )
}

export default Pool
